import React, { useState, useEffect } from "react";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import api from "../../api";
import Error from "../error/Error";
import { CaseItem } from "../case/Case";
import { MailItem } from "../mail/Mail";
import { TagItem } from "../tags/tag";
import { AttachmentItem } from "../attachments/Attachment";
import SaveMailPage from "../readmainpages/SaveMailPage";
import SaveTimesheetPage from "../readmainpages/SaveTimesheetPage";
import Footer from "../footer/Footer";
import Header, { UserInfo } from "../header/Header";
import { TimesheetTemplateItem } from "../timesheet_template/TimesheetTemplate";

/* global Office */

export enum ReadMainPages {
  SaveMailPage,
  SaveTimesheetPage,
}

const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

type ReadMainProps = {
  disconnect: () => void;
};

const ReadMain: React.FC<ReadMainProps> = (props: ReadMainProps) => {
  const { disconnect } = props;
  const [error, setError] = useState<string>("");
  const [page, setPage] = useState<ReadMainPages>(ReadMainPages.SaveMailPage);
  const [selectedCase, setSelectedCase] = useState<CaseItem | null>(null);
  const [mail, setMail] = useState<MailItem | null>(null);
  const [tags, setTags] = useState<TagItem[]>([]);
  const [attachments, setAttachments] = useState<AttachmentItem[]>([]);
  const [userInfo, setUserInfo] = useState<UserInfo>(null);
  const [selectedTimesheetTemplate, setSelectedTimesheetTemplate] = useState<TimesheetTemplateItem>(null);
  const [timesheetTemplates, setTimesheetTemplates] = useState<TimesheetTemplateItem[]>([]);

  const getReadMainData = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getReadData,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      response = JSON.parse(await request.promise); 
    } catch (error) {
      setError(String(error));
    }
    return response; 
  };

  const getData = async () => {
    // Get mail data
    const item = Office.context.mailbox.item;
    const mailItem = {
      id: Office.context.mailbox.convertToRestId(item.itemId, Office.MailboxEnums.RestVersion.v1_0),
      subject: item.subject,
    };
    setMail(mailItem); 
    // Get attachments data
    const officeAttachmentDetails = Office.context.mailbox.item.attachments;
    const attachmentItems = officeAttachmentDetails.map((details) => ({
      name: details.name,
      id: details.id,
    }));
    setAttachments(attachmentItems);
    // Get read main data
    const response = await getReadMainData();
    if (response.result.success) {
      // setCases(response.result.data.cases);
      setTags(response.result.data.tags);
      setTimesheetTemplates(response.result.data.timesheet_templates);
      setUserInfo(response.result.data.user_info);
    } else {
      setError(response.result.error);
    };
  };

  const loadData = () => {
    getData();
    setPage(ReadMainPages.SaveMailPage);
  };

  useEffect(() => {
    loadData();
  }, []);

  // const onMailSaved = (selectedCase: CaseItem) => {
  //   setSelectedCase(selectedCase);
  // };

  const saveMailPage = (
    <SaveMailPage
      // onMailSaved={onMailSaved}
      goToPage={setPage} 
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      mail={mail}
      tags={tags}
      attachments={attachments}
      getConnectionToken={getConnectionToken}
    />
  );

  const saveTimesheetPage = (
    <SaveTimesheetPage
      goToPage={setPage} 
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      timesheetTemplates={timesheetTemplates}
      selectedTimesheetTemplate={selectedTimesheetTemplate}
      onTimesheetTemplateSelected={setSelectedTimesheetTemplate}
      getConnectionToken={getConnectionToken}
    />
  );

  let wrapPage = (page: React.ReactNode) => (
    <>
      <Header userInfo={userInfo} loadData={loadData}/>
      <div className="page">
        <Error error={error} setError={setError} />
        {page}
      </div>
      <Footer disconnect={disconnect} />
    </>
  );

  switch (page) {
    case ReadMainPages.SaveMailPage:
      return wrapPage(saveMailPage);
    case ReadMainPages.SaveTimesheetPage:
      return wrapPage(saveTimesheetPage);
    default:
      return wrapPage(saveMailPage);
  }
};

export default ReadMain;
