import * as React from "react";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import { _t } from "../../../utils/Translator";

export interface AttachmentItem {
  name: string;
  id: string;
  size?: number; // Add size property for validation
  isInline?: boolean; // Add isInline property for inline attachments
  contentType?: string; // Add contentType property for MIME type checks
}

export interface AttachmentProps {
  attachment: AttachmentItem;
  tags: TagItem[];
  onAttachmentRemove: (attachment: AttachmentItem) => void;
}

export interface AttachmentState {
  name: string;
  oversizeWarning?: string; // Added for warnings
}

export default class Attachment extends React.Component<AttachmentProps, AttachmentState> {
  tagsRef: React.RefObject<Tags>;

  constructor(props, context) {
    super(props, context);
    this.state = {
      name: (props.attachment && props.attachment.name) || "",
      oversizeWarning: undefined,
    };
    this.tagsRef = React.createRef();
  }

  getAttachmentData = () => {
    return new Promise<any>((resolve, reject) => {
      const SIZE_THRESHOLD_SINGLE = 10 * 1024 * 1024; // 10 MB size limit
      const { attachment } = this.props;

      // Validate size and provide a warning if oversized
      if (attachment.size && attachment.size > SIZE_THRESHOLD_SINGLE) {
        this.setState({
          oversizeWarning: _t(
            "Warning: Attachment size exceeds the limit of %(size)s MB.",
            { size: SIZE_THRESHOLD_SINGLE / (1024 * 1024) }
          ),
        });
        resolve({
          attachment,
          name: this.state.name,
          content: null,
          tags: this.tagsRef.current ? this.tagsRef.current.getTagsData() : [],
          oversize: true,
        });
        return;
      }

      // Fetch content using getAttachmentContentAsync
      Office.context.mailbox.item.getAttachmentContentAsync(attachment.id, (asyncResult) => {
        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
          resolve({
            attachment,
            name: this.state.name,
            content: asyncResult.value.content,
            tags: this.tagsRef.current ? this.tagsRef.current.getTagsData() : [],
            oversize: false,
          });
        } else {
          console.error(`Failed to fetch attachment content for ${attachment.name}:`, asyncResult.error.message);
          reject(new Error(`Failed to fetch content for ${attachment.name}`));
        }
      });
    });
  };

  remove = () => {
    this.props.onAttachmentRemove(this.props.attachment);
  };

  render() {
    return (
      <div className="card">
        <div className="card-body mt-3">
          {this.state.oversizeWarning && (
            <div className="text-danger mb-2">{this.state.oversizeWarning}</div>
          )}
          <div className="input-group">
            <input
              className="form-control"
              type="text"
              value={this.state.name}
              onChange={(e) => this.setState({ name: e.target.value })}
            />
            <button
              className="btn btn-primary"
              type="button"
              aria-label={_t("Close")}
              onClick={this.remove}
            >
              <i className="fa fa-trash" aria-hidden="true" />
            </button>
          </div>
          <div className="mt-2">
            <Tags ref={this.tagsRef} tags={this.props.tags}></Tags>
          </div>
        </div>
      </div>
    );
  }
}
