import * as React from "react";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import { _t } from "../../../utils/Translator";

export interface MailItem {
  subject: string;
  id: string;
}

export interface MailProps {
  mail: MailItem;
  tags: TagItem[];
}

export interface MailState {
  added: boolean;
  mail_subject: string;
}

export default class Mail extends React.Component<MailProps, MailState> {
  tagsRef;
  constructor(props, context) {
    super(props, context);
    this.state = {
      added: true,
      mail_subject: (props.mail && props.mail.subject) || "",
    };
    this.tagsRef = React.createRef();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.mail && this.props.mail && prevProps.mail.id !== this.props.mail.id) {
      this.setState({
        mail_subject: this.props.mail.subject || "",
      });
    }
  }

  resetMail = () => {
    this.setState({
      added: true,
      mail_subject: (this.props.mail && this.props.mail.subject) || "",
    });
    if (this.tagsRef.current) {
      this.tagsRef.current.resetTags();
    }
  };

  extractEmailAddresses = (recipients: any) => {
    if (!recipients) {
      return [];
    }
    if (Array.isArray(recipients)) {
      return recipients.map((recipient) => recipient.emailAddress);
    }
    return [recipients.emailAddress];
  };

  getEmailBody = (format: Office.CoercionType): Promise<string> => {
    return new Promise((resolve, reject) => {
      const item = Office.context.mailbox.item;

      if (!item) {
        reject("No email item found.");
        return;
      }

      item.body.getAsync(format, (asyncResult) => {
        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
          resolve(asyncResult.value);
        } else {
          reject(asyncResult.error.message);
        }
      });
    });
  };

  fetchAttachmentContent = (attachment): Promise<any> => {
    return new Promise((resolve) => {
      if (!attachment.id) {
        console.error(`Invalid attachment ID for: ${attachment.name}`);
        resolve({
          name: attachment.name,
          inline: attachment.isInline && attachment.contentType.indexOf("image") >= 0,
          oversize: true,
        });
      } else if (attachment.size > 10 * 1024 * 1024) {
        // Skip oversized attachments
        resolve({
          name: attachment.name,
          inline: attachment.isInline && attachment.contentType.indexOf("image") >= 0,
          oversize: true,
        });
      } else {
        Office.context.mailbox.item.getAttachmentContentAsync(attachment.id, (asyncResult) => {
          if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
            resolve({
              name: attachment.name,
              content: asyncResult.value.content,
              inline: attachment.isInline && attachment.contentType.indexOf("image") >= 0,
              oversize: false,
            });
          } else {
            console.error(`Failed to fetch attachment: ${attachment.name}`);
            resolve({
              name: attachment.name,
              inline: attachment.isInline && attachment.contentType.indexOf("image") >= 0,
              oversize: true,
            });
          }
        });
      }
    });
  };

  getAttachmentData = async () => {
    const attachments = Office.context.mailbox.item.attachments;

    if (!attachments || attachments.length === 0) {
      console.log("No attachments found.");
      return [];
    }

    const SIZE_THRESHOLD_TOTAL = 40 * 1024 * 1024; // 40 MB
    let totalSize = attachments.reduce((sum, attachment) => sum + attachment.size, 0);

    if (totalSize > SIZE_THRESHOLD_TOTAL) {
      console.error(`Total attachment size exceeds ${SIZE_THRESHOLD_TOTAL / (1024 * 1024)} MB`);
      return [];
    }

    const attachmentPromises = attachments.map((attachment) => this.fetchAttachmentContent(attachment));

    try {
      return await Promise.all(attachmentPromises);
    } catch (error) {
      console.error("Error fetching attachments:", error);
      return [];
    }
  };

  processInlineAttachments = (body: string, attachments) => {
    const doc = new DOMParser().parseFromString(body, "text/html");
    const imageElements = doc.getElementsByTagName("img");

    let index = 0;
    attachments.forEach((attachment) => {
      if (attachment.inline) {
        if (attachment.oversize) {
          imageElements[index]?.setAttribute(
            "alt",
            _t(`Image ${attachment.name} not displayed due to size limits.`)
          );
        } else {
          const fileExtension = attachment.name.split(".").pop();
          imageElements[index]?.setAttribute(
            "src",
            `data:image/${fileExtension};base64, ${attachment.content}`
          );
        }
        index++;
      }
    });

    return doc.body.innerHTML;
  };

  getMailData = async () => {
    if (!this.state.added) {
      return {};
    }

    const item = Office.context.mailbox.item;
    const [htmlBody, plainTextBody] = await Promise.all([
      this.getEmailBody(Office.CoercionType.Html),
      this.getEmailBody(Office.CoercionType.Text),
    ]);

    const attachments = await this.getAttachmentData();

    const processedHtmlBody = this.processInlineAttachments(htmlBody, attachments);

    return {
      mail: this.props.mail,
      from: item.from.emailAddress,
      to: this.extractEmailAddresses(item.to),
      cc: this.extractEmailAddresses(item.cc),
      bcc: this.extractEmailAddresses(item.bcc),
      timeCreated: item.dateTimeCreated.toUTCString(),
      htmlBody: processedHtmlBody,
      plainTextBody: plainTextBody,
      subject: this.state.mail_subject,
      tags: this.tagsRef.current.getTagsData(),
      attachments: attachments.filter((att) => !att.inline),
    };
  };

  add = () => {
    this.setState({ added: true, mail_subject: (this.props.mail && this.props.mail.subject) || "" });
  };

  remove = () => {
    this.setState({ added: false });
  };

  render() {
    if (this.state.added) {
      return (
        <div>
          <div className="page-section-title">{_t("Email")}</div>
          <div className="card">
            <div className="card-body">
              <div className="input-group mt-3">
                <input
                  className="form-control"
                  type="text"
                  value={this.state.mail_subject}
                  onChange={(e) => this.setState({ mail_subject: e.target.value })}
                />
                <button
                  className="btn btn-primary"
                  type="button"
                  aria-label={_t("Close")}
                  onClick={this.remove}
                >
                  <i className="fa fa-trash" aria-hidden="true" />
                </button>
              </div>
              <div className="mt-2">
                <Tags ref={this.tagsRef} tags={this.props.tags}></Tags>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <>
          <div className="page-section-title">{_t("Email")}</div>
          <div className="mt-3">
            <button className="btn btn-outline-primary btn-sm" type="button" onClick={this.add}>
              <i className="fa fa-plus-circle" aria-hidden="true"></i>&nbsp;{_t("Add e-mail")}
            </button>
          </div>
        </>
      );
    }
  }
}